import { ICartChange, IProduct } from "Shared/ts/interfaces/cart-change";
import {
    convertFragmentToHTMLElement,
    createElement
} from "Shared/ts/utils/html";
import HTMLTemplateRenderingEngine from "Shared/ts/utils/html-template-rendering-engine";
import PubSub from "Shared/ts/patterns/pubsub";
import { PubSubTask } from "Shared/ts/interfaces/patterns/pubsub";
import {
    ICustomerCartChangeSubscription,
    ICustomerCartProductItemSubscription,
    IRenderProductViewOptions
} from "Shared/ts/interfaces/customer-cart";

export type ExtendCartChangeInterfaceTask = (cart: ICartChange) => ICartChange;

export type FilterProductsArrayTask = (products: IProduct[]) => IProduct[];

export default class CustomerCart {
    private static htmlTemplateRenderingEngine: HTMLTemplateRenderingEngine =
        new HTMLTemplateRenderingEngine();

    private static customerCartContainerTemplateRepository: WeakMap<
        CustomerCart,
        HTMLTemplateElement
    > = new WeakMap<CustomerCart, HTMLTemplateElement>();

    private static customerCartItemTemplateRepository: WeakMap<
        CustomerCart,
        HTMLTemplateElement
    > = new WeakMap<CustomerCart, HTMLTemplateElement>();

    private static customerCartContainerRepository: WeakMap<
        CustomerCart,
        HTMLElement
    > = new WeakMap<CustomerCart, HTMLElement>();

    private static pubSubRepository: WeakMap<CustomerCart, PubSub> =
        new WeakMap<CustomerCart, PubSub>();

    /**
     * Represents the id of the Customer Cart instance
     */
    public id: string;

    /**
     * Represents the image path that contains the product images
     */
    private static imagePath: string;

    private static cartChangeExtensionRepository: WeakMap<
        CustomerCart,
        ExtendCartChangeInterfaceTask
    > = new WeakMap<CustomerCart, ExtendCartChangeInterfaceTask>();

    private static productArrayFilterRepository: WeakMap<
        CustomerCart,
        FilterProductsArrayTask
    > = new WeakMap<CustomerCart, FilterProductsArrayTask>();

    private static customerCartViewExtensionRepository: WeakMap<
        CustomerCart,
        ExtendCartChangeInterfaceTask
    > = new WeakMap<CustomerCart, ExtendCartChangeInterfaceTask>();

    constructor(id: string, imagePath: string = "images/Site1/products/") {
        this.id = id;

        CustomerCart.imagePath = imagePath;
        CustomerCart.pubSubRepository.set(this, new PubSub());

        CustomerCart.initializeCustomerCartContainerTemplate(this);
        CustomerCart.initializeCustomerCartItemTemplate(this);
        CustomerCart.initializeCustomerCartContainer(this);
        CustomerCart.initializeCartChangeEvent(this);
    }

    public subscribeToCartProductItems(task: PubSubTask): void {
        const pubsub = CustomerCart.pubSubRepository.get(this);
        if (!pubsub) return;

        pubsub.subscribe("cartProductItems", task);
    }

    public subscribeToCartChangeEvent(task: PubSubTask): void {
        const pubsub = CustomerCart.pubSubRepository.get(this);
        if (!pubsub) return;

        pubsub.subscribe("cartChange", task);
    }

    public extendCartChangeInterface(task: ExtendCartChangeInterfaceTask) {
        CustomerCart.cartChangeExtensionRepository.set(this, task);
    }

    public filterProductsArray(task: FilterProductsArrayTask) {
        CustomerCart.productArrayFilterRepository.set(this, task);
    }

    public extendCustomerCartView(task: ExtendCartChangeInterfaceTask) {
        CustomerCart.customerCartViewExtensionRepository.set(this, task);
    }

    public containsItemsInCart(): boolean {
        const container =
            CustomerCart.customerCartContainerRepository.get(this);
        if (!container) return false;

        return /true/i.test(
            container.getAttribute("data-customer-cart-contains-items") ?? ""
        );
    }

    private static initializeCartChangeEvent(context: CustomerCart): void {
        window.addEventListener(
            "CartChange",
            (event: CustomEvent<ICartChange>): void => {
                const cart =
                    this.cartChangeExtensionRepository.get(context)?.(
                        event.detail
                    ) ?? event.detail;

                this.handleCustomerCart(cart, context).then(() => {
                    const items =
                        this.productArrayFilterRepository.get(context)?.(
                            event.detail.items
                        ) ?? event.detail.items;

                    this.handleCustomerCartItems(items, context);

                    const customerCartViewData =
                        this.customerCartViewExtensionRepository.get(context)?.(
                            event.detail
                        ) ?? event.detail;

                    this.handleCustomerCartViews(customerCartViewData, context);

                    this.notifyCartChangeSubscribers(cart, context);
                    this.notifyCartProductItemSubscribers(items, context);
                });
            }
        );
    }

    private static notifyCartChangeSubscribers(
        cart: ICartChange,
        context: CustomerCart
    ): void {
        const pubsub = this.pubSubRepository.get(context);
        if (!pubsub) return;

        const container =
            CustomerCart.customerCartContainerRepository.get(context);
        if (!container) return;

        const data: ICustomerCartChangeSubscription = {
            cart,
            container
        };

        pubsub.publish("cartChange", data);
    }

    private static notifyCartProductItemSubscribers(
        items: IProduct[],
        context: CustomerCart
    ): void {
        const pubsub = this.pubSubRepository.get(context);
        if (!pubsub) return;

        const container =
            CustomerCart.customerCartContainerRepository.get(context);
        if (!container) return;

        const data: ICustomerCartProductItemSubscription = {
            items,
            container
        };

        pubsub.publish("cartProductItems", data);
    }

    private static handleCustomerCartViews(
        cart: ICartChange,
        context: CustomerCart
    ): void {
        const container =
            CustomerCart.customerCartContainerRepository.get(context);
        if (!container) return;

        switch (cart.items?.length > 0) {
            case true:
                container.setAttribute(
                    "data-customer-cart-contains-items",
                    "true"
                );
                break;
            case false:
                container.setAttribute(
                    "data-customer-cart-contains-items",
                    "false"
                );
                break;
        }
    }

    private static handleCustomerCart(
        cart: ICartChange,
        context: CustomerCart
    ): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            const container =
                CustomerCart.customerCartContainerRepository.get(context);
            if (!container) return;

            const containerDeposit =
                container.querySelector<HTMLElement>(
                    `[data-customer-cart-deposit="template"]`
                ) ?? container;

            if (container.hasAttribute("data-customer-cart-container-ready")) {
                this.htmlTemplateRenderingEngine.renderViewByElement(
                    containerDeposit,
                    cart
                );

                return resolve();
            }

            const template =
                this.customerCartContainerTemplateRepository.get(context);
            if (!template) return reject("The template could not be found");

            const view = this.htmlTemplateRenderingEngine.renderViewByTemplate(
                template,
                cart
            );
            if (!view) return reject();

            const element = convertFragmentToHTMLElement(view);
            if (!element)
                return reject(
                    "There was a problem converting the document fragment to an HTML element"
                );

            const elementDeposit =
                element.querySelector<HTMLElement>(
                    `[data-customer-cart-deposit="template"]`
                ) ?? element;

            containerDeposit.innerHTML = elementDeposit.innerHTML;

            container.setAttribute(
                "data-customer-cart-container-ready",
                "true"
            );

            return resolve();
        });
    }

    private static handleCustomerCartItems(
        items: IProduct[],
        context: CustomerCart
    ): void {
        const container =
            CustomerCart.customerCartContainerRepository.get(context);
        if (!container) return;

        const itemCodes = items.map((item) => item.code);

        const cartItems = Array.from<HTMLElement>(
            container.querySelectorAll(
                `[data-customer-cart-item-list] [id^="${context.id}-customer-cart-item"][data-customer-cart-product-code]`
            )
        );

        const cartItemCodes = cartItems
            .map((cartItem) =>
                cartItem.getAttribute("data-customer-cart-product-code")
            )
            .filter((cartItemCode): cartItemCode is string => !!cartItemCode);

        const addedItems = items.filter(
            (item) => !cartItemCodes.includes(item.code)
        );

        addedItems.forEach((item) =>
            context.renderProductView(item.code, item)
        );

        const removedItems = cartItemCodes.filter(
            (cartItemCode) => !itemCodes.includes(cartItemCode)
        );

        removedItems.forEach((item) => context.removeProductView(item));

        const existingItems = items.filter(
            (item) =>
                cartItemCodes.includes(item.code) &&
                item.props.find(
                    (prop) => prop.Key === "NoEdit" && prop.Value === "True"
                )
        );

        existingItems
            .map((item) => {
                return {
                    item,
                    cartItem: cartItems.find(
                        (cartItem) =>
                            cartItem.getAttribute(
                                "data-customer-cart-product-code"
                            ) === item.code
                    )
                };
            })
            .filter(
                (group): group is { item: IProduct; cartItem: HTMLElement } =>
                    !!group.cartItem
            )
            .forEach((group) =>
                CustomerCart.htmlTemplateRenderingEngine.renderViewByElement(
                    group.cartItem,
                    group.item
                )
            );
    }

    private static initializeCustomerCartContainerTemplate(
        context: CustomerCart
    ): void {
        const candidate = document.querySelector<HTMLTemplateElement>(
            `template[data-customer-cart-container-id="${context.id}"]`
        );
        if (!candidate) return;

        this.customerCartContainerTemplateRepository.set(context, candidate);
    }

    private static initializeCustomerCartItemTemplate(
        context: CustomerCart
    ): void {
        const candidate = document.querySelector<HTMLTemplateElement>(
            `template[data-customer-cart-item-id="${context.id}"]`
        );
        if (!candidate) return;

        this.customerCartItemTemplateRepository.set(context, candidate);
    }

    private static initializeCustomerCartContainer(
        context: CustomerCart
    ): void {
        const candidate = document.querySelector<HTMLElement>(`#${context.id}`);
        if (!candidate) return;

        this.customerCartContainerRepository.set(context, candidate);
    }

    private static getProductImageByProductCode(
        productCode: string
    ): Promise<string> {
        return new Promise<string>((resolve, reject) => {
            fetch(
                `/Services/GetProductImageByProductCode.ashx?productCode=${productCode}&imagePath=${this.imagePath}`
            )
                .then((response) => response.text())
                .then((html) => resolve(html))
                .catch((error) =>
                    reject(`There was an issue capturing the html`)
                );
        });
    }

    public removeProductView(
        productCode: string,
        insertionContainer?: HTMLElement
    ): void {
        const container =
            CustomerCart.customerCartContainerRepository.get(this);
        if (!container) return;

        const candidate =
            insertionContainer ??
            container.querySelector("[data-customer-cart-item-list]");
        if (!candidate) return;

        const record = candidate.querySelector(
            `[data-customer-cart-product-code="${productCode}"]`
        );
        if (!record) return;

        record.remove();
    }

    /**
     * Takes an object of product data from the CartChange event along with an optional HTMLTemplateElement and an HTMLElement to perform the following actions: 1. Use the HTMLTemplateElement to render an HTMLElement using the product data. 2. Use the HTMLElement as the insertion container to render the new HTMLElement in the document.
     * @param product IProduct
     * @param templateView HTMLTemplateElement
     * @param insertionContainer HTMLElement
     * @returns void
     */
    public renderProductView<T extends Record<string, any>>(
        productCode: string,
        productData: T,
        options?: IRenderProductViewOptions
    ): void {
        const container =
            CustomerCart.customerCartContainerRepository.get(this);

        if (!container) return;

        const itemList =
            options?.insertionContainer ??
            container.querySelector<HTMLElement>(
                "[data-customer-cart-item-list]"
            );
        if (!itemList) return;

        const id = `${this.id}-customer-cart-item-${productCode}`;
        const record = itemList.querySelector(
            `[data-customer-cart-product-code="${productCode}"]`
        );
        if (record) return;

        const template =
            options?.templateView ??
            CustomerCart.customerCartItemTemplateRepository.get(this);
        if (!template) return;

        const view =
            CustomerCart.htmlTemplateRenderingEngine.renderViewByTemplate(
                template,
                productData
            );
        if (!view) return;

        const entry = createElement("div", {
            id,
            "data-customer-cart-product-code": productCode
        });
        entry.appendChild(view);

        options?.insertionOrder?.(itemList, entry) ??
            itemList.insertAdjacentElement("afterbegin", entry);

        const picture = entry.querySelector<HTMLElement>(
            "[data-customer-cart-item-picture]"
        );
        if (!picture) return;

        CustomerCart.getProductImageByProductCode(productCode).then((html) => {
            picture.innerHTML = html;
        });
    }
}
