var initializeCustomerCartUI = function initializeCustomerCartUI(container) {
  var categoryProductCarousels = Array.from(container.querySelectorAll(".slide--category-products-customer-cart"));
  categoryProductCarousels.forEach(function (categoryProductCarousel) {
    var carousel = new Carousel(new SlideCarouselAdapter(categoryProductCarousel));
    carousel.enablePrevNextControls();
    carousel.enableThumbnailControls();
  });
  var pictureAnimationCarousels = Array.from(container.querySelectorAll(".slide--product-beauty-shot"));
  pictureAnimationCarousels.forEach(function (carousel) {
    return new FadeCarouselWithPictureVimeoAnimation(carousel);
  });
};

var captureCustomerCartUI = function captureCustomerCartUI() {
  var process = false;
  pubsub.subscribe("captureBody", function (elements) {
    var container = elements.find(function (element) {
      return element.id === customerCart.id;
    });
    if (!container) return;
    var hasAttribute = container.hasAttribute("data-customer-cart-container-ready");

    if (hasAttribute) {
      return initializeCustomerCartUI(container);
    }

    if (process) return;
    process = true;
    var captureContainer = new CaptureElement(container);
    captureContainer.subscribe("attributes", function (record) {
      if (record.attributeName !== "data-customer-cart-container-ready") return;
      initializeCustomerCartUI(container);
    });
  });
};

