var flyoutRepo = initializeGlobalFlyoutRepository();

var initializeNav = function initializeNav() {
  var nav = document.querySelector("#nav");
  if (!nav) return;
  initializeScrollingNav(nav);
  initializeNavWithFlyoutSupport(nav);
};

var initializeScrollingNav = function initializeScrollingNav(nav) {
  observer("[data-nav-tracker]", {
    outRange: function outRange(element) {
      nav.classList.add("nav--is-minibar");
    },
    unObserve: false
  });
};

var initializeNavWithFlyoutSupport = function initializeNavWithFlyoutSupport(nav) {
  initializeFlyoutsThenNav(".flyout", nav.id);
  initializeFlyoutUI();
  var containers = Array.from(nav.querySelectorAll(".flyout"));
  var hoverableContainers = containers.filter(function (flyout) {
    return flyout.hasAttribute("data-flyout-allow-hover");
  });
  var usingNavDrawerUI = false;
  new MediaQuery("(max-width: 75rem)").inbound(function (task) {
    hoverableContainers.forEach(function (container) {
      return container.removeAttribute("data-flyout-allow-hover");
    });
    usingNavDrawerUI = true;
  }).outbound(function (task) {
    hoverableContainers.forEach(function (container) {
      return container.setAttribute("data-flyout-allow-hover", "true");
    });
    usingNavDrawerUI = false;
  });
  var searchContainer = containers.find(function (container) {
    return container.id === "search-form";
  });
  if (!searchContainer) return;
  var searchFlyout = flyoutRepo.get(searchContainer);
  if (!searchFlyout) return;
  addEventListener("click", function (event) {
    var target = event.target;
    var meetsCondition = target.closest("#search-form") === searchContainer && !searchContainer.classList.contains("flyout--is-hidden");
    if (meetsCondition) return;
    searchFlyout.off();
  });
  var searchController = searchContainer.querySelector(".flyout__button");
  if (!searchController) return;
  var captureSearchController = new CaptureElement(searchController);
  captureSearchController.subscribe("attributes", function (record) {
    if (record.attributeName !== "aria-expanded") return;
    if (usingNavDrawerUI) return;

    switch (searchController.getAttribute("aria-expanded")) {
      case "true":
        hoverableContainers.forEach(function (container) {
          return container.removeAttribute("data-flyout-allow-hover");
        });
        break;

      case "false":
        hoverableContainers.forEach(function (container) {
          return container.setAttribute("data-flyout-allow-hover", "true");
        });
        break;
    }
  });
};

