var createInstagramGallery = function createInstagramGallery(gallery) {
  if (!gallery) return;
  Promise.all(instagramIdList.map(function (id) {
    return instagramMedia.requestMediaNode(id);
  })).then(function (media) {
    Array.from(gallery.querySelectorAll("img[src]")).forEach(function (picture, index) {
      var post = media[index];
      picture.setAttribute("src", post.media_type === "VIDEO" ? post.thumbnail_url : post.media_url);
      var image = picture.querySelector("img");
      if (!image) return;
      image.alt = post.caption;
    });
  }).catch(function (error) {
    return console.debug(error);
  });
};

