// api
import ModalDialogIframe from "Shared/ts/api/modal/modal-dialog-iframe";

// observers
import { observer } from "Shared/ts/observers/intersection";

// utils
import CaptureElement from "Shared/ts/utils/capture-element";
import {
    createElement,
    getJSONByElementAttribute,
    setElementAttributes
} from "Shared/ts/utils/html";
import LoadItem from "Shared/ts/utils/load-item";
import VimeoManager from "Shared/ts/utils/vimeo-manager";

// applications
import { pubsub } from "Shared/ts/applications/delay-input";

export default class ModalDialogVimeoIframe extends ModalDialogIframe {
    private static vimeoManager = new VimeoManager();

    constructor() {
        super();

        ModalDialogVimeoIframe.initializeVimeoPostersByControllers();
        ModalDialogVimeoIframe.autoPauseVimeoPlayerOnClose();
    }

    private static initializeVimeoPostersByControllers() {
        observer(
            '[data-modal-dialog-iframe^="https://player.vimeo.com/video/"]',
            {
                inRange: (placeholder) => {
                    const url = placeholder.getAttribute(
                        "data-modal-dialog-iframe"
                    ) as string;

                    const img = placeholder.getAttribute("data-src-img");
                    if (!img) return;

                    const id = this.vimeoManager.getIdByUrl(url);
                    if (!id) return;

                    const attributes =
                        getJSONByElementAttribute<HTMLImageElement>(
                            placeholder,
                            "data-attr"
                        );

                    const min = 400;
                    const width =
                        attributes.width > min ? attributes.width : min;
                    const height =
                        attributes.height > min ? attributes.height : min;

                    this.vimeoManager
                        .generatePosterImage(id, {
                            width: width.toString(),
                            height: height.toString()
                            // quality: "100"
                        })
                        .then((posterImgUrl) => {
                            placeholder.setAttribute(
                                "data-src-img",
                                posterImgUrl
                            );
                        })
                        .finally(() => {
                            placeholder.classList.add("vimeo-player-button");

                            const playIconElement = createElement("span", {
                                class: "vimeo-player-button__play"
                            });

                            placeholder.insertAdjacentElement(
                                "afterbegin",
                                playIconElement
                            );

                            new LoadItem(placeholder).load(() =>
                                this.vimeoManager
                                    .createoEmbedById(id)
                                    .then((response) => {
                                        const metadata = JSON.parse(
                                            response.response
                                        );

                                        placeholder.setAttribute(
                                            "data-modal-dialog-title",
                                            metadata.title
                                        );

                                        const img =
                                            placeholder.querySelector("img");

                                        if (!img) return;

                                        img.alt = metadata.title;
                                    })
                            );
                        });
                }
            }
        );
    }

    private static autoPauseVimeoPlayerOnClose() {
        pubsub.subscribe("captureBody", (elements: Element[]) => {
            const modalDialogContainers = elements.filter((element) =>
                element.classList.contains("modal-dialog--video")
            );

            modalDialogContainers.forEach((modalDialogContainer) => {
                const iframe =
                    modalDialogContainer.querySelector<HTMLIFrameElement>(
                        "iframe"
                    );
                if (!iframe) return;

                setElementAttributes(iframe, {
                    allowfullscreen: "allowfullscreen",
                    webkitallowfullscreen: "webkitallowfullscreen",
                    allow: "fullscreen"
                });

                iframe.setAttribute("src", iframe.src);

                const player = this.vimeoManager.createPlayerByIframe(iframe);

                Promise.all([
                    player.getVideoWidth(),
                    player.getVideoHeight()
                ]).then(function (dimensions) {
                    const width = dimensions[0];
                    const height = dimensions[1];

                    setElementAttributes(iframe, {
                        width,
                        height
                    });

                    const container = iframe.closest<HTMLElement>(
                        "[data-vimeo-iframe-container]"
                    );
                    if (!container) return;

                    container.setAttribute(
                        "style",
                        `${
                            container.getAttribute("style") ?? ""
                        } --vimeo-iframe-width:${width}; --vimeo-iframe-height:${height};`
                    );
                });

                const captureContainer = new CaptureElement(
                    modalDialogContainer
                );

                captureContainer.subscribe("attributes", (record) => {
                    if (
                        record.attributeName === "hidden" &&
                        modalDialogContainer.hasAttribute("hidden")
                    ) {
                        player.pause();
                    }
                });
            });
        });
    }
}
