import IInstagramMediaDTMAdapter from "Shared/ts/interfaces/instagram-media-dtm-adapter";
import IInstagramMediaManager, {
    IInstagramMedia
} from "Shared/ts/interfaces/instagram-media-manager";
import InstagramMediaManager from "Shared/ts/utils/instagram-media-manager";

export default class InstagramMediaDTMAdapter {
    private static accessToken: WeakMap<InstagramMediaDTMAdapter, string> =
        new WeakMap();

    private static instagramMediaManager: WeakMap<
        InstagramMediaDTMAdapter,
        IInstagramMediaManager
    > = new WeakMap();

    constructor() {}

    private static getInstagramManagerByContext(
        context: InstagramMediaDTMAdapter
    ): IInstagramMediaManager | undefined {
        return this.instagramMediaManager.get(context);
    }

    private static getAccessTokenByContext(
        context: InstagramMediaDTMAdapter
    ): string | undefined {
        return this.accessToken.get(context);
    }

    private static getInstagramManagerOrSetByContext(
        context: InstagramMediaDTMAdapter
    ): Promise<IInstagramMediaManager | undefined> {
        return new Promise<IInstagramMediaManager | undefined>(
            (resolve, reject) => {
                const manager = this.getInstagramManagerByContext(context);

                if (manager) {
                    resolve(manager);
                } else {
                    this.getAccessTokenOrRequest(context)
                        .then((token) => {
                            this.instagramMediaManager.set(
                                context,
                                new InstagramMediaManager(token)
                            );

                            resolve(this.instagramMediaManager.get(context));
                        })
                        .catch((error) => reject(error));
                }
            }
        );
    }

    private static getAccessTokenOrRequest(
        context: InstagramMediaDTMAdapter
    ): Promise<string> {
        return new Promise<string>((resolve, reject) => {
            const token = this.getAccessTokenByContext(context);

            if (token) resolve(token);

            fetch("/Instagram/gettoken")
                .then(
                    (response) =>
                        response.json() as Promise<IInstagramMediaDTMAdapter>
                )
                .then((response) => {
                    if (!response.token) {
                        return reject({
                            message: `The returned access token is null.`,
                            context
                        });
                    }

                    if (response.token === "") {
                        return reject({
                            message: `The returned access token is empty.`,
                            context
                        });
                    }

                    this.accessToken.set(context, response.token);

                    resolve(response.token);
                })
                .catch((error) =>
                    reject({
                        message: `The access token could not be obtained within the context`,
                        context
                    })
                );
        });
    }

    public requestMedia(): Promise<IInstagramMedia[]> {
        return new Promise<IInstagramMedia[]>((resolve, reject) => {
            InstagramMediaDTMAdapter.getInstagramManagerOrSetByContext(this)
                .then((manager) => {
                    if (!manager) {
                        reject({
                            message: `The Instagram Media Manager could not be obtained within the context`,
                            context: this
                        });

                        return;
                    }

                    manager
                        .requestMedia()
                        .then((media) => resolve(media))
                        .catch((error) => reject(error));
                })
                .catch((error) => reject(error));
        });
    }

    public requestImages(): Promise<IInstagramMedia[]> {
        return new Promise<IInstagramMedia[]>((resolve, reject) => {
            InstagramMediaDTMAdapter.getInstagramManagerOrSetByContext(this)
                .then((manager) => {
                    if (!manager) {
                        reject({
                            message: `The Instagram Media Manager could not be obtained within the context`,
                            context: this
                        });

                        return;
                    }

                    manager
                        .requestImages()
                        .then((media) => resolve(media))
                        .catch((error) => reject(error));
                })
                .catch((error) => reject(error));
        });
    }

    public requestMediaNode(mediaId: string): Promise<IInstagramMedia> {
        return new Promise<IInstagramMedia>((resolve, reject) => {
            InstagramMediaDTMAdapter.getInstagramManagerOrSetByContext(this)
                .then((manager) => {
                    if (!manager) {
                        reject({
                            message: `The Instagram Media Manager could not be obtained within the context`,
                            context: this
                        });

                        return;
                    }

                    manager
                        .requestMediaNode(mediaId)
                        .then((media) => resolve(media))
                        .catch((error) => reject(error));
                })
                .catch((error) => reject(error));
        });
    }
}
