import { ICartChange } from "Shared/ts/interfaces/cart-change";
import { IOrderItem } from "Shared/ts/interfaces/dtm-shopping-cart";

export interface IShoppingCartEvent {
    productCode: string;
    quantity: number;
    button: HTMLButtonElement | HTMLInputElement;
}

interface CustomEventMap {
    "shopping-cart::add": CustomEvent<IShoppingCartEvent>;
    "shopping-cart::update": CustomEvent<IShoppingCartEvent>;
    "shopping-cart::increment": CustomEvent<IShoppingCartEvent>;
    "shopping-cart::remove": CustomEvent<IShoppingCartEvent>;
}

declare global {
    interface Window {
        addEventListener<K extends keyof CustomEventMap>(
            type: K,
            listener: (this: Window, ev: CustomEventMap[K]) => void
        ): void;
    }
}

export default class ShoppingCart {
    /**
     * Represents the state of the current target element that was activated by the user. This element is used to perform an internal-cooldown after interaction.
     */
    private static currentTarget: HTMLElement;

    constructor() {
        ShoppingCart.initializeRequestEvent();
        ShoppingCart.initializeResponseEvent();
    }

    private static addActionEvent = new CustomEvent<IShoppingCartEvent>(
        "shopping-cart::add",
        {
            detail: <IShoppingCartEvent>{}
        }
    );

    private static incrementActionEvent = new CustomEvent<IShoppingCartEvent>(
        "shopping-cart::increment",
        {
            detail: <IShoppingCartEvent>{}
        }
    );

    private static updateActionEvent = new CustomEvent<IShoppingCartEvent>(
        "shopping-cart::update",
        {
            detail: <IShoppingCartEvent>{}
        }
    );

    private static removeActionEvent = new CustomEvent<IShoppingCartEvent>(
        "shopping-cart::remove",
        {
            detail: <IShoppingCartEvent>{}
        }
    );

    private static dispatchCartEvent(
        event: CustomEvent<IShoppingCartEvent>,
        data: IShoppingCartEvent
    ): void {
        event.detail.productCode = data.productCode;
        event.detail.quantity = data.quantity;
        event.detail.button = data.button;

        window.dispatchEvent(event);
    }

    /**
     * Responsible for defining a global click event that determines if the target belongs to either of the following actions: `add`, `update` or `remove` based on the `data-shopping-cart-action` HTML attribute. The appropriate action then provides the customer cart with the updated product quantity through the `window.handleCartChange` method.
     */
    private static initializeRequestEvent(): void {
        window.addEventListener("click", (event) => {
            const target = event.target as HTMLButtonElement;

            const element = this.getTargetByAttribute(
                target
            ) as HTMLButtonElement;
            if (!element) return;

            const productCode = this.getProductCodeByTarget(element);
            if (!productCode) return;

            let quantity;

            switch (true) {
                case this.isTargetAddAction(element):
                    quantity = this.getCurrentQuantity(element);
                    if (typeof quantity !== "number") return;

                    this.handleCartChangeWithInternalCooldown(
                        element,
                        productCode,
                        quantity
                    );

                    this.dispatchCartEvent(this.addActionEvent, {
                        button: element,
                        productCode,
                        quantity
                    });
                    break;
                case this.isTargetIncrementAction(element):
                    let currentItemQuantity = this.lookupItemQuantity(
                        productCode,
                        0
                    );
                    quantity = this.getCurrentQuantity(element);
                    if (typeof quantity !== "number") return;
                    quantity += currentItemQuantity;

                    this.handleCartChangeWithInternalCooldown(
                        element,
                        productCode,
                        quantity
                    );

                    this.dispatchCartEvent(this.incrementActionEvent, {
                        button: element,
                        productCode,
                        quantity
                    });
                    break;
                case this.isTargetUpdateAction(element):
                    quantity = this.updateButtonQuantity(element);
                    if (typeof quantity !== "number") return;

                    this.dispatchCartEvent(this.updateActionEvent, {
                        button: element,
                        productCode,
                        quantity
                    });

                    if (
                        this.isTargetControlledByAddAction(element) ||
                        this.isTargetControlledByIncrementAction(element)
                    )
                        return;

                    this.handleCartChangeWithInternalCooldown(
                        element,
                        productCode,
                        quantity
                    );
                    break;
                case this.isTargetRemoveAction(element):
                    this.handleCartChangeWithInternalCooldown(
                        element,
                        productCode,
                        0
                    );

                    this.dispatchCartEvent(this.removeActionEvent, {
                        button: element,
                        productCode,
                        quantity: 0
                    });
                    break;
            }
        });

        window.addEventListener("keyup", (event) => {
            const target = event.target as HTMLInputElement;

            const element = this.getTargetByAttribute(target);
            if (!element) return;

            const productCode = this.getProductCodeByTarget(element);
            if (!productCode) return;

            const quantity = this.getCurrentQuantityByQuantityElement(target);

            this.handleCartChangeWithInternalCooldown(
                element,
                productCode,
                quantity
            );

            this.dispatchCartEvent(this.updateActionEvent, {
                button: element,
                productCode,
                quantity
            });
        });
    }

    /**
     * Responsible for defining a global `CartChange` event handler that scans the HTML document for any elements where the id represents the product code quantity and attempts to update the value to match the network response of `/Index/Cart/Edit`.
     */
    private static initializeResponseEvent(): void {
        window.addEventListener(
            "CartChange",
            (event: CustomEvent<ICartChange>) => {
                this.enableInteractionForCurrentTarget();

                const items = event.detail.items;

                items.forEach((item) => {
                    const candidates = Array.from(
                        document.querySelectorAll<HTMLInputElement>(
                            `[id^=${item.code}Qty]`
                        )
                    );

                    candidates.forEach((candidate) => {
                        if (this.hasSyncWithCartAttribute(candidate)) {
                            candidate.valueAsNumber = item.qty;
                        }
                    });

                    item.qty === item.minQty || item.qty === 1
                        ? this.restrictMinusAction(item.id)
                        : this.lifeMinusAction(item.id);

                    item.qty === item.maxQty
                        ? this.restrictAddAction(item.id)
                        : this.liftAddAction(item.id);
                });
            }
        );
    }

    /**
     * Takes the provided HTMLElement as the current target and assigns the `disabled` HTML attribute to prevent further interaction by the user along with a custom HTML attribute `data-shopping-cart-state` to provide a customized user-interface.
     * @param target HTMLElement
     * @param productCode string
     * @param quantity number
     */
    private static handleCartChangeWithInternalCooldown(
        target: HTMLElement,
        productCode: string,
        quantity: number
    ): void {
        this.disableInteractionByCurrentTarget(target, "processing");

        window.handleCartChange(productCode, quantity);
    }

    /**
     * Takes an HTML element and assigns the `disabled` HTML attribute along with a custom HTML attribute `data-shopping-cart-state` to provide a customized user-interface.
     * @param target HTMLElement
     * @param state string
     */
    private static disableInteraction(
        target: HTMLElement,
        state: string
    ): void {
        target.setAttribute("data-shopping-cart-state", state);
        target.setAttribute("disabled", "");
    }

    /**
     * Responsible for clearing the internal cooldown of the curren target HTML element. The element can be interacted by the user afterwards.
     */
    private static enableInteraction(target: HTMLElement): void {
        target.removeAttribute("data-shopping-cart-state");
        target.removeAttribute("disabled");
    }

    /**
     * Enables user-interaction on the current target HTML element.
     */
    private static enableInteractionForCurrentTarget(): void {
        if (!this.currentTarget) return;

        this.enableInteraction(this.currentTarget);
    }

    /**
     * Responsible for clearing the internal cooldown of the curren target HTML element. The element can be interacted by the user afterwards.
     */
    private static disableInteractionByCurrentTarget(
        target: HTMLElement,
        state: string
    ): void {
        this.disableInteraction(target, state);
        this.currentTarget = target;
    }

    /**
     * Attempts to locate and return the target element that contains the HTML attribute `data-shopping-cart-action`.
     * @param target HTMLButtonElement
     * @returns HTMLButtonElement | null
     */
    private static getTargetByAttribute(
        target: HTMLButtonElement | HTMLInputElement
    ): HTMLButtonElement | HTMLInputElement | null {
        return target.hasAttribute("data-shopping-cart-action")
            ? target
            : target.closest("[data-shopping-cart-action]");
    }

    /**
     * Extracts and returns the actor name from the element containing the HTML attribute `data-shopping-cart-action`.
     * @param target HTMLElement
     * @returns string | null
     */
    private static getActorByTarget(target: HTMLElement): string | null {
        return target.getAttribute("data-shopping-cart-action");
    }

    /**
     * Extracts and returns the product code from the element containing the HTML attribute `data-shopping-cart-action`.
     * @param target HTMLElement
     * @returns string | null
     */
    private static getProductCodeByTarget(
        target: HTMLElement | HTMLInputElement
    ): string | null {
        return target.getAttribute("data-shopping-cart-product-code");
    }

    /**
     * Determines if the current action represents a remove action.
     * @param target HTMLElement
     * @returns boolean
     */
    private static isTargetRemoveAction(target: HTMLElement): boolean {
        return this.getActorByTarget(target)?.toLowerCase() === "remove";
    }

    /**
     * Determines if the current action represents an update action.
     * @param target HTMLElement
     * @returns boolean
     */
    private static isTargetUpdateAction(target: HTMLElement): boolean {
        return this.getActorByTarget(target)?.toLowerCase() === "update";
    }

    /**
     * Determines if the current action represents an add action.
     * @param target HTMLElement
     * @returns boolean
     */
    private static isTargetAddAction(target: HTMLElement): boolean {
        return this.getActorByTarget(target)?.toLowerCase() === "add";
    }

    private static isTargetIncrementAction(target: HTMLElement): boolean {
        return this.getActorByTarget(target)?.toLowerCase() == "increment";
    }

    /**
     * Determines if the current element is controlled by another HTML element representing the add action.
     * @param target HTMLElement
     * @returns boolean
     */
    private static isTargetControlledByAddAction(target: HTMLElement): boolean {
        return this.isTargetControlledByAction(target, "add");
    }

    /**
     * Determines if the current element is controlled by another HTML element representing the increment action.
     * @param target HTMLElement
     * @returns boolean
     */
    private static isTargetControlledByIncrementAction(
        target: HTMLElement
    ): boolean {
        return this.isTargetControlledByAction(target, "increment");
    }

    private static isTargetControlledByAction(
        target: HTMLElement,
        action: string
    ): boolean {
        const input = this.getCurrentQuantityInputElement(target);
        if (!input) return false;

        const id = input.id;
        if (!id) return false;

        return document.body.contains(
            document.querySelector(
                `[data-shopping-cart-action="${action}"][aria-controls*="${input.id}"]`
            )
        );
    }

    /**
     * Attempts to return the HTML element containing the current product quantity.
     * @param element HTMLElement
     * @returns HTMLInputElement | null
     */
    private static getCurrentQuantityInputElement(
        element: HTMLElement
    ): HTMLInputElement | null {
        const quantityId = element.getAttribute("aria-controls");
        if (!quantityId) return null;

        return document.querySelector<HTMLInputElement>(`#${quantityId}`);
    }

    /**
     * Attempts to return the current product quantity by the current HTML element.
     * @param element HTMLElement
     * @returns number | undefined
     */
    private static getCurrentQuantity(
        element: HTMLElement
    ): number | undefined {
        const input = this.getCurrentQuantityInputElement(element);
        if (!input) return;

        return this.getCurrentQuantityByQuantityElement(input);
    }

    /**
     * Attempts to return the current product quantity by the current HTML Input element.
     * @param input HTMLInputElement
     * @returns number
     */
    private static getCurrentQuantityByQuantityElement(
        input: HTMLInputElement
    ): number {
        return input.valueAsNumber;
    }

    /**
     * Using an associated HTML input element, the current product quantity is updated based on whether the current HTML button element accepts the HTML attribute `data-qty-modifier` and values at either `plus` or `minus`. The updated product quantity is then returned.
     * @param element HTMLButtonElement
     * @returns number | undefined
     */
    private static updateButtonQuantity(
        element: HTMLButtonElement
    ): number | undefined {
        const input = this.getCurrentQuantityInputElement(element);
        if (!input) return;

        const quantityModifier = element.getAttribute(
            "data-shopping-cart-quantity-modifier"
        );

        let newQuantity = this.getCurrentQuantityByQuantityElement(input);

        switch (quantityModifier) {
            case "plus":
                newQuantity++;
                break;
            case "minus":
                newQuantity--;
                break;
            default:
                break;
        }

        return this.updateInputQuantity(input, newQuantity);
    }

    /**
     * Using a proposed quantity value, the HTML input element applies the min-max constraints using `data-min-qty` and `data-max-qty` to ensure the value remains protected within those constraints. When within the constraints, the proposed quantity value is assigned to the HTML input element.
     * @param input HTMLInputElement
     * @param newQuantity number
     * @returns number | undefined
     */
    private static updateInputQuantity(
        input: HTMLInputElement,
        newQuantity: number
    ): number | undefined {
        const minQty = input.min;
        const maxQty = input.max;
        const productCode = this.getProductCodeByTarget(input);

        if (!minQty || !maxQty || !productCode) return;

        const minQuantity = parseInt(minQty);
        const maxQuantity = parseInt(maxQty);

        switch (true) {
            case newQuantity <= minQuantity:
                newQuantity = 1;
                break;
            case newQuantity > maxQuantity:
                newQuantity = maxQuantity;
                break;
            default:
                this.liftAddAction(productCode);
                break;
        }

        if (isNaN(newQuantity)) {
            newQuantity = this.lookupItemQuantity(productCode, 1);
        }

        input.valueAsNumber = newQuantity;

        return newQuantity;
    }

    /**
     * Relieves the ability to add a product to the shopping cart due to the current quantity staying within range.
     * @param productCode string
     */
    private static liftAddAction(productCode: string): void {
        const candidates = Array.from<HTMLElement>(
            document.querySelectorAll(
                `[data-shopping-cart-product-code="${productCode}"][data-shopping-cart-action="add"], [data-shopping-cart-product-code="${productCode}"][data-shopping-cart-action="update"][data-shopping-cart-quantity-modifier="plus"]`
            )
        );

        candidates.forEach((candidate) => this.enableInteraction(candidate));
    }

    /**
     * Restricts the ability to add a product to the shopping cart due to the current quantity exceeding the maximum quantity allowed.
     * @param productCode string
     */
    private static restrictAddAction(productCode: string): void {
        const candidates = Array.from<HTMLElement>(
            document.querySelectorAll(
                `[data-shopping-cart-product-code="${productCode}"][data-shopping-cart-action="add"], [data-shopping-cart-product-code="${productCode}"][data-shopping-cart-action="update"][data-shopping-cart-quantity-modifier="plus"]`
            )
        ).filter((candidate) => {
            const input = this.getCurrentQuantityInputElement(candidate);

            return input ? this.hasSyncWithCartAttribute(input) : false;
        });

        candidates.forEach((candidate) =>
            this.disableInteraction(candidate, "disable")
        );
    }

    /**
     * Restricts the ability to subtract a product from the shopping cart due to the current quantity falling below the minimum quantity allowed.
     * @param productCode string
     */
    private static restrictMinusAction(productCode: string): void {
        const candidates = Array.from<HTMLElement>(
            document.querySelectorAll(
                `[data-shopping-cart-product-code="${productCode}"][data-shopping-cart-action="subtract"], [data-shopping-cart-product-code="${productCode}"][data-shopping-cart-action="update"][data-shopping-cart-quantity-modifier="minus"]`
            )
        ).filter((candidate) => {
            const input = this.getCurrentQuantityInputElement(candidate);

            return input ? this.hasSyncWithCartAttribute(input) : false;
        });

        candidates.forEach((candidate) =>
            this.disableInteraction(candidate, "disable")
        );
    }

    /**
     * Relieves the ability to subtract a product from the shopping cart due to the current quantity staying within range.
     * @param productCode string
     */
    private static lifeMinusAction(productCode: string): void {
        const candidates = Array.from<HTMLElement>(
            document.querySelectorAll(
                `[data-shopping-cart-product-code="${productCode}"][data-shopping-cart-action="subtract"], [data-shopping-cart-product-code="${productCode}"][data-shopping-cart-action="update"][data-shopping-cart-quantity-modifier="minus"]`
            )
        );

        candidates.forEach((candidate) => this.enableInteraction(candidate));
    }

    /**
     * Takes a product code and determines what the current quantity is from the customer cart; otherwise, the provided default quantity is returned.
     * @param productCode string
     * @param defaultQuantity number
     * @returns number
     */
    private static lookupItemQuantity(
        productCode: string,
        defaultQuantity: number
    ): number {
        const item = this.getOrderItemByProductCode(productCode);

        return item?.qty ? item?.qty : defaultQuantity;
    }

    /**
     * Takes a product code and determines if the product code is currently in the customer cart.
     * @param productCode string
     * @returns IOrderItem | undefined
     */
    private static getOrderItemByProductCode(
        productCode: string
    ): IOrderItem | undefined {
        const items = window._dtmShoppingCart.Items();

        return items.find((item) => item.id === productCode);
    }

    private static hasSyncWithCartAttribute(target: HTMLElement): boolean {
        const syncAttribute = "data-shopping-cart-sync";

        return target.hasAttribute(syncAttribute)
            ? target.getAttribute(syncAttribute)?.toLowerCase() === "true"
            : false;
    }
}
