import CaptureElement from "Shared/ts/utils/capture-element";

export default class KlaviyoSignUpForm {
    public controllers: Element[] = [];

    constructor() {
        if (this.hasPixel()) {
            this.captureControllerElements();
        }
    }

    private static captureKlaviyo(): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            const captureHead = new CaptureElement(document.head);

            captureHead.subscribe("childList", (record) => {
                captureHead.disconnect();

                const nodes = Array.from(record.addedNodes).filter(
                    (node) => node.nodeType === Node.ELEMENT_NODE
                ) as Element[];

                const resource = nodes.find((node) =>
                    node.outerHTML.includes("klaviyo")
                );

                if (resource && this.hasKlaviyoApi()) {
                    resolve();
                } else {
                    captureHead.connect();
                }
            });
        });
    }

    public hasPixel(): boolean {
        return document.contains(
            document.querySelector<HTMLScriptElement>(
                'script[src^="//static.klaviyo.com/onsite/js/klaviyo.js"]'
            )
        );
    }

    private static hasKlaviyoApi(): boolean {
        return "_klOnsite" in window;
    }

    public hasKlaviyo(): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            const isInstalled = KlaviyoSignUpForm.hasKlaviyoApi();

            if (isInstalled) resolve();

            KlaviyoSignUpForm.captureKlaviyo()
                .then(() => resolve())
                .catch((error) => reject(error));
        });
    }

    public captureControllerElements(): void {
        this.controllers = Array.from(
            document.querySelectorAll(
                "[data-klaviyo-form-id], [data-klaviyo-form]"
            )
        );

        this.controllers.forEach((controller) => {
            controller.addEventListener("click", () => {
                const id = controller.getAttribute("data-klaviyo-form-id");
                if (!id) return;

                this.hasKlaviyo().then(() => {
                    window._klOnsite.push(["openForm", id]);
                });
            });

            this.hasKlaviyo().then(() => {
                controller.setAttribute("data-klaviyo-ui-state", "ready");
                controller.removeAttribute("hidden");
            });
        });
    }
}
