// utils
import HTMLTemplateRenderingEngine from "Shared/ts/utils/html-template-rendering-engine";
import KlaviyoManager from "Shared/ts/utils/klaviyo-manager";

export default class KlaviyoTemplateController extends KlaviyoManager {
    /**
     * Represents a static instance of the `HTMLTemplateRenderingEngine`
     */
    private static htmlTemplateRenderingEngine: HTMLTemplateRenderingEngine =
        new HTMLTemplateRenderingEngine();

    /**
     * Represents a collection of HTML templates
     */
    private static templates: HTMLTemplateElement[] = [];

    /**
     * Represents a key-value relationship between the Klaviyo view id and the HTML element that the id is assigned to
     */
    private static idViewRepository: Map<string, HTMLElement> = new Map<
        string,
        HTMLElement
    >();

    /**
     * Represents the unique id value of the HTML template
     */
    private id: string;

    /**
     * Represents the placeholder element that will contain the Klaviyo view
     */
    private placeholder: HTMLElement;

    /**
     * Represents the template containing the Klaviyo view
     */
    private _template: HTMLTemplateElement | null = null;

    /**
     * Represents the template containing the Klaviyo view. Debug errors will be logged to the browser console to inform the developer if the template element is not visible in the document or if the template doesn't contain a unique id attribute.
     */
    public set template(value: HTMLTemplateElement | null) {
        if (value === null) {
            KlaviyoTemplateController.reportDebugError(
                `The HTML template element representing the Klaviyo waitlist could not be located in the document. Aborting task.`
            );
            return;
        }

        this._template = value;

        const id = value?.id;
        if (!id) {
            KlaviyoManager.reportDebugError(
                `The HTML template element requires the id attribute for assignment. Aborting task.`
            );
            return;
        }

        this.id = id;
    }

    public get template(): HTMLTemplateElement | null {
        return this._template;
    }

    /**
     * Represents an event that can dispatch a custom event to the developer when the Klaviyo view changes
     */
    private static viewChangeEvent: CustomEvent = new CustomEvent("viewchange");

    /**
     * Represents an event that can dispatch a custom event to the developer when the Klaviyo view is ready
     */
    private static viewReadyEvent: CustomEvent = new CustomEvent("viewready");

    /**
     * Takes an HTML template as a candidate along with an optional placeholder element and renders the template contents as the *Klaviyo view* and inserts those contents into the placeholder. To control the Klaviyo view, HTML elements containing the required attributes (*elected as controllers*) can be activated by the user.
     *
     * To elect an HTML element as a controller, the following attributes must be provided:
     *
     * 1. `data-klaviyo-product-catalog-id` (*Represents the `CampaignProductView` id Guid*)
     * 2. `data-klaviyo-view-id` (*Represents the Klaviyo view element id*)
     * 3. `data-klaviyo-template-id` (*Represents the Klaviyo template element id*)
     *
     * @param template HTMLTemplateElement
     * @param placeholder HTMLElement
     */
    constructor(
        template: HTMLTemplateElement,
        placeholder: HTMLElement = document.body
    ) {
        super();

        this.template = template;
        this.id = template.id;
        this.placeholder = placeholder;

        KlaviyoTemplateController.templates.push(template);
        KlaviyoTemplateController.initializeControllerEventsByContext(this);
    }

    /**
     * Takes a button element and returns a promise for a response containing that button after the Klaviyo view has succesfully changed.
     * @param controller HTMLButtonElement
     * @returns Promise<HTMLButtonElement>
     */
    public updateViewByController(
        controller: HTMLButtonElement
    ): Promise<HTMLButtonElement> {
        return new Promise<HTMLButtonElement>((resolve, reject) => {
            const id =
                controller.getAttribute("data-klaviyo-product-catalog-id") ??
                "";

            this.getProductFromCatalogByProductId(id)
                .then((product) => {
                    const viewIds = (
                        controller.getAttribute("data-klaviyo-view-id") ?? ""
                    ).split(" ");

                    viewIds
                        .filter((viewId) =>
                            KlaviyoTemplateController.idViewRepository.has(
                                viewId
                            )
                        )
                        .map(
                            (viewId) =>
                                KlaviyoTemplateController.idViewRepository.get(
                                    viewId
                                ) as HTMLElement
                        )
                        .forEach((view) =>
                            KlaviyoTemplateController.htmlTemplateRenderingEngine.renderViewByElement(
                                view,
                                product
                            )
                        );

                    const entries = viewIds.filter(
                        (reference) =>
                            !KlaviyoTemplateController.idViewRepository.has(
                                reference
                            )
                    );

                    const templateIds = (
                        controller.getAttribute("data-klaviyo-template-id") ??
                        ""
                    ).split(" ");

                    entries
                        .map((viewId) => {
                            return {
                                viewId,
                                template:
                                    KlaviyoTemplateController.templates.find(
                                        (template) =>
                                            templateIds.includes(template.id)
                                    )
                            };
                        })
                        .filter((entry) => entry.template !== null)
                        .forEach((entry) => {
                            const fragment =
                                KlaviyoTemplateController.htmlTemplateRenderingEngine.renderViewByTemplate(
                                    entry.template,
                                    product
                                );
                            if (!fragment) return;

                            this.placeholder.appendChild(fragment);

                            const view = document.querySelector<HTMLElement>(
                                `#${entry.viewId}`
                            );

                            if (!view) return;

                            KlaviyoTemplateController.idViewRepository.set(
                                entry.viewId,
                                view
                            );
                        });

                    resolve(controller);
                })
                .catch((error) => {
                    KlaviyoTemplateController.reportDebugError(
                        `The product id did not return a match from the catalog.`,
                        {
                            controller,
                            id
                        }
                    );

                    reject(error);
                });
        });
    }

    /**
     * Elects the first controller element to initialize the Klaviyo view.
     * @returns void
     */
    public initializeViewByDefault(): void {
        const firstController = document.querySelector<HTMLButtonElement>(
            "[data-klaviyo-product-catalog-id]"
        );
        if (!firstController) return;

        this.updateViewByController(firstController);
    }

    /**
     * Takes the current instance of the `KlaviyoTemplateController` to provide event-delegation on the the controllers that are capable of changing the Klaviyo view.
     * @param context KlaviyoTemplateController
     */
    private static initializeControllerEventsByContext(
        context: KlaviyoTemplateController
    ): void {
        addEventListener("click", (event) => {
            const target = event.target as HTMLButtonElement;

            const controller =
                target.closest<HTMLButtonElement>(
                    "[data-klaviyo-product-catalog-id]"
                ) ?? target;
            if (!controller.hasAttribute("data-klaviyo-product-catalog-id"))
                return;

            const templateId =
                context.template?.getAttribute(
                    "data-klaviyo-product-catalog-id"
                ) ?? "";

            const controllerId =
                controller.getAttribute("data-klaviyo-product-catalog-id") ??
                "";

            if (templateId === controllerId) return;

            context.placeholder.dispatchEvent(this.viewChangeEvent);

            context.updateViewByController(controller).then(() => {
                context.template?.setAttribute(
                    "data-klaviyo-product-catalog-id",
                    controllerId
                );

                context.placeholder.dispatchEvent(this.viewReadyEvent);
            });
        });
    }
}
